.container {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
}

.form {
    background-color: lightblue;
    text-align: left;
    width: 420px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 0.5em;
    padding: 1em;
    border-radius: 0.5em;
}

.error {
    color: red;
    font-size: small;
}